/* Custom styles for transparent Aurora conversation */
.aurora-conversation {
  background: transparent !important;
  border-radius: 50% !important;
  overflow: hidden !important;
}

/* Make the main conversation container transparent */
.aurora-conversation .container {
  background: transparent !important;
  border-radius: 50% !important;
}

/* Style the video container to be circular */
.aurora-conversation .videoContainer {
  background: transparent !important;
  border-radius: 50% !important;
  overflow: hidden !important;
}

/* Hide the default footer controls since we have external ones */
.aurora-conversation .footer {
  display: none !important;
}

/* Style the main video to be circular */
.aurora-conversation .mainVideoContainer {
  border-radius: 50% !important;
  overflow: hidden !important;
  background: transparent !important;
}

/* Hide self view inside the conversation since we have external controls */
.aurora-conversation .selfViewContainer {
  display: none !important;
}

/* Make the main video fill the circular container */
.aurora-conversation .mainVideo {
  border-radius: 50% !important;
  object-fit: cover !important;
  width: 100% !important;
  height: 100% !important;
}

/* Style waiting/connecting state */
.aurora-conversation .waitingContainer {
  background: transparent !important;
  border-radius: 50% !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  color: #00D9FF !important;
  font-family: 'SF Mono', monospace !important;
}

/* Hide error messages or style them appropriately */
.aurora-conversation .errorContainer {
  background: rgba(220, 38, 38, 0.1) !important;
  border: 1px solid rgba(220, 38, 38, 0.3) !important;
  border-radius: 8px !important;
  color: #ff6b6b !important;
  padding: 12px !important;
  margin: 8px !important;
  font-size: 12px !important;
}

/* Ensure any audio elements are hidden */
.aurora-conversation audio {
  display: none !important;
}

/* Style any loading states */
.aurora-conversation .loading {
  background: transparent !important;
  color: #00D9FF !important;
}

/* Custom video element styling for avatar */
.aurora-conversation video {
  border-radius: 50% !important;
  object-fit: cover !important;
  transform: scale(1.1) !important;
  background: transparent !important;
}

/* Ensure the iframe or any containers don't interfere */
.aurora-conversation iframe,
.aurora-conversation div {
  border-radius: inherit !important;
}

/* Hide any Daily.js UI elements we don't want */
.aurora-conversation [data-daily] {
  border-radius: 50% !important;
}

/* Ensure proper z-indexing */
.aurora-conversation {
  z-index: 1 !important;
}